/**
 * CSS custom properties
 *
 * @since 1.0.0
 */

:root {
	// colors
	--color-primary-dark: #f6c800;
	--color-primary: #f9d900;
	--color-black: #1f1b1c;
	--color-gray-darkest: #272425;
	--color-gray-darker: #292929;
	--color-gray-dark: #3c3a3b;
	--color-gray: #928e8f;
	--color-gray-mid-light: #c7c7c7;
	--color-gray-light: #e5e5e5;
	--color-white: #fff;

	// fonts
	--font-family-global: 'Roboto', sans-serif;
	--font-family-condensed: 'Roboto Condensed', sans-serif;
	--font-family-heading: 'Bebas Neue', sans-serif;

	// font sizes + line heights
	--font-size-base: 16;

	--font-size-regular: calc(var(--font-size-base) / 16 * 1rem);
	--font-size-medium: calc(28 / var(--global-font-size) * 1rem);
	--font-size-large: calc(38 / var(--global-font-size) * 1rem);
	--font-size-xlarge: calc(60 / var(--global-font-size) * 1rem);

	--line-height-base: 1.6;
	--line-height-heading: 1.2;

	// spacing
	--content-width: 90%;
	--content-width-max: 87.5rem;

	// transitions
	--transition-duration: 200ms;
	--transition-timing-function: ease-in-out;
}
